<template><HelloWorld :resData="resData" msg="Welcome to Your Vue.js App" /></template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import {ref} from 'vue'
export default {
  name: 'App',
  components: {
    HelloWorld
  },
  setup() {
    const resData = ref('NAN')
    const mqtt = require("mqtt");
    const client = mqtt.connect('ws://127.0.0.1:7000', {
      username: "user",
      password: '123456'
    });

    client.on("connect", function () {
      console.log("服务器连接成功");
      console.log(client.options.clientId);
      client.subscribe("text", { qos: 1 }); // 订阅text消息
    });

    client.on("message", function (top, message) {
      console.log("当前topic：", top);
      console.log(message.toString());
      resData.value = message.toString()
    })
    return {
      resData
    }

  }
}
</script>

